.Login{width:100%;height:100vh}.Login .header{display:flex;margin:0 15px;flex-direction:column;justify-content:center;align-items:center;text-align:center;padding-top:40px}.Login .header img{min-width:300px;max-width:400px;width:30%;transition:all 0.25s ease}.Login .header img:hover{transform:scale(1.095);filter:brightness(95%)}.Login .header h1{font-family:"Roboto Thin", sans-serif;font-weight:800;margin-top:15px;font-size:2.5em}.Login .form-container{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:70px 12.5% 0 12.5%;border-radius:25px;background-color:var(--navbar-background-color)}.Login .form-container .form{display:table;padding:50px 0;width:75%;text-align:center}.Login .form-container .form h1{margin-bottom:25px;font-size:2em}.Login .form-container .form p,.Login .form-container .form input{font-size:20px}.Login .form-container .form .button{-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;display:inline-block;padding:.75rem 1.8rem;border-radius:10rem;color:#fff;text-transform:uppercase;font-size:1rem;letter-spacing:.2rem;transition:all .3s;position:relative;overflow:hidden;z-index:1}.Login .form-container .form .button:after{content:'';position:absolute;bottom:0;left:0;width:100%;height:100%;background-color:#0cf;border-radius:10rem;z-index:-2}.Login .form-container .form .button:before{content:'';position:absolute;bottom:0;left:0;width:0;height:100%;background-color:#008fb3;transition:all .3s;border-radius:10rem;z-index:-1}.Login .form-container .form .button:hover{color:#fff}.Login .form-container .form .button:hover:before{width:100%}.Login .form-container .form .LoginSelect{text-align:left;-webkit-user-select:none;-ms-user-select:none;user-select:none;margin:25px 0;display:flex;flex-direction:column}.Login .form-container .form .LoginSelect p,.Login .form-container .form .LoginSelect input{font-size:19px;font-family:Roboto, sans-serif;font-weight:300}.Login .form-container .form .LoginSelect .underlined{transition:border-bottom-color ease 0.25s;padding-bottom:5px;border:none;background:none;border-bottom:1px solid rgba(255,255,255,0.2)}.Login .form-container .form .LoginSelect .underlined:hover{border-bottom:1px solid rgba(255,255,255,0.4)}.Login .form-container .form .LoginSelect .underlined:focus,.Login .form-container .form .LoginSelect .underlined:active{border-bottom:1px solid rgba(255,255,255,0.9)}.Login .form-container .form .LoginSelect .input,.Login .form-container .form .LoginSelect .box{margin-top:10px}.Login .form-container .form .LoginSelect .input:first-of-type{margin-top:20px}.Login .form-container .form .LoginSelect .input :-ms-input-placeholder{color:var(--navbar-entry-color)}.Login .form-container .form .LoginSelect .input ::placeholder{color:var(--navbar-entry-color)}.Login .form-container .form .LoginSelect .box{display:flex;flex-direction:row}.Login .form-container .form .LoginSelect .box .icon{margin-left:5px;align-self:center;justify-self:right;transform:scale(1.45);transition:all 0.4s}.Login .form-container .form .LoginSelect .box .icon.rotated{transform:scale(1.45) rotate(-180deg)}.Login .form-container .form .LoginSelect .dropdown-container{width:100%}.Login .form-container .form .LoginSelect .dropdown-container .dropdown{margin-top:5px;position:static;display:none;width:100% !important}.Login .form-container .form .LoginSelect .dropdown-container .dropdown .dropdown-selection{background-color:var(--login-drop-down-background-color);color:var(--navbar-entry-color-hover);border-radius:7px}.Login .form-container .form .LoginSelect .dropdown-container .dropdown .dropdown-selection *{font-size:18px;border-radius:7px;padding:5px 10px}.Login .form-container .form .LoginSelect .dropdown-container .dropdown .dropdown-selection *:hover{background-color:rgba(0,0,0,0.26)}.Login .form-container .form .LoginSelect .dropdown-container .dropdown.shown{display:flex;flex-direction:column}

.Nav{background:var(--navbar-background-color);display:table-cell;vertical-align:top;min-width:300px;max-width:400px;width:20%;height:100vh}.Nav .header{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-top:20px}.Nav .header img{max-width:250px;width:75%;transition:all 0.25s ease}.Nav .header img:hover{transform:scale(1.095);filter:brightness(95%)}.Nav .header h1{font-family:"Roboto Thin", sans-serif;font-weight:800;margin-top:15px;font-size:2em}.Nav .links{margin-top:40px}.NavEntry{display:flex;margin:10px 20px;padding:0;border-radius:15px;color:var(--navbar-entry-color);background:var(--navbar-entry-background-color);transition:all 0.25s, background-color 0.2s, color 0.1s, border-radius 0.25s}.NavEntry:hover{color:var(--navbar-entry-color-hover);background:var(--navbar-entry-background-color-hover)}.NavEntry.active{color:var(--navbar-entry-color-current);background:var(--navbar-entry-background-color-current)}.NavEntry:hover{border-radius:20px;transform:scale(1.085, 1.025)}.NavEntry *{margin-top:10px;margin-bottom:10px}.NavEntry :first-child{margin-left:18%}.NavEntry :last-child{margin-right:18%}.NavEntry .icon{width:50px;height:auto}.NavEntry .name{width:100%;text-align:center;font-family:'Quicksand', sans-serif;font-weight:500;font-size:22px}

.Loader{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.Loader .lds-ripple{display:inline-block;position:relative;width:80px;height:80px}.Loader .lds-ripple div{position:absolute;border:4px solid #fff;opacity:1;border-radius:50%;animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}.Loader .lds-ripple div:nth-child(2){animation-delay:-0.5s}@keyframes lds-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}.Loader .lds-ellipsis{display:inline-block;position:relative;width:80px;height:80px}.Loader .lds-ellipsis div{position:absolute;top:33px;width:13px;height:13px;border-radius:50%;background:#fff;animation-timing-function:cubic-bezier(0, 1, 1, 0)}.Loader .lds-ellipsis div:nth-child(1){left:8px;animation:lds-ellipsis1 0.6s infinite}.Loader .lds-ellipsis div:nth-child(2){left:8px;animation:lds-ellipsis2 0.6s infinite}.Loader .lds-ellipsis div:nth-child(3){left:32px;animation:lds-ellipsis2 0.6s infinite}.Loader .lds-ellipsis div:nth-child(4){left:56px;animation:lds-ellipsis3 0.6s infinite}@keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(24px, 0)}}

.Dashboard-Component{height:100%;width:100%}.Card{margin:100px;display:table;padding:35px;background:var(--card-background-color);border-radius:20px}.Card .icon{filter:brightness(85%);color:var(--card-name-color);width:45px;height:auto}.Card .name{font-family:"Roboto Light", sans-serif;font-weight:500;font-size:18px;margin-top:-5px;color:var(--card-name-color)}.Card .value{font-family:'Quicksand', sans-serif;font-weight:600;font-size:25px;margin-top:10px}

:root{--navbar-background-color: #2F363E;--navbar-entry-color: #8E979E;--navbar-entry-background-color-current: #24292D;--navbar-entry-color-current: #EEEEEE;--navbar-entry-background-color-hover: #292f36;--navbar-entry-color-hover: #EEEEEE;--background-color: #24292D;--card-background-color: #292e36;--card-name-color: #b2b7bf;--login-drop-down-background-color: #2b3039;--accent-color-1: #1E293B;--scroll-background-color: rgba(0, 0, 0, 0);--scroll-background-color-hover: rgba(0, 0, 0, 0.09);--scroll-thumb-color: rgba(139, 139, 139, 0.5);--scroll-thumb-color-click: rgba(139, 139, 139, 0.35)}


* {
	margin: 0;
	padding: 0;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
	text-decoration: none;
}

input:focus, textarea:focus, select:focus, button {
	outline: none;
}

.App {
	background: var(--background-color);
	display: table;
	width: 100%;
	height: 100vh;
}
.content {
	display: flex;
	height: 100vh;
	width: 100%;
	overflow: auto;
}


::-webkit-scrollbar {
	width: 8px;
	background-color: var(--scroll-background-color);
	-webkit-border-radius: 100px;
}

/* hover effect for both scrollbar area, and scrollbar 'thumb' */
::-webkit-scrollbar:hover {
	background-color: var(--scroll-background-color-hover);
}

/* The scrollbar 'thumb' ...that marque oval shape in a scrollbar */
::-webkit-scrollbar-thumb:vertical {
	background: var(--scroll-thumb-color);
	-webkit-border-radius: 100px;
}

::-webkit-scrollbar-thumb:vertical:active {
	background: var(--scroll-thumb-color-click);
	-webkit-border-radius: 100px;
}

